import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { GlobalProvider } from '../global/global';
import { UserProvider } from '../user/user';
import 'rxjs/add/operator/map';

/*
  Generated class for the AuthProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class AuthProvider {

  reqOpts;
  private loggedIn: boolean = false;
  //private options:RequestOptions = new RequestOptions({headers: this.headers });
  
  constructor(
    private userProvider: UserProvider,
    private gv: GlobalProvider,
    public http: HttpClient) {
    console.log('Hello AuthProvider Provider');
    this.reqOpts = this._initializeReqOpts(this.reqOpts);
    this.reqOpts = this. _addStandardHeaders(this.reqOpts.headers);
  }

  private _addStandardHeaders(header:HttpHeaders)
  {
    header = header.append('Content-Type','application/json');
    header = header.append('Accept','application/json');
    header = header.append('Access-Control-Allow-Origin', '*');
    header = header.append('Access-Control-Allow-Methods', 'GET,POST');
    header = header.append('Access-Control-Allow-Headers', 'Authorization, Content-Type');
    return header;
  }

  private _initializeReqOpts(reqOpts)
  {
    if (!reqOpts) {
      reqOpts = {
       headers: new HttpHeaders(),
       params: new HttpParams()
      };
    }
    return reqOpts;
  }

  loginUser(formLogin){
    return this.http.post(""+this.gv.getApiLogin(), JSON.stringify(formLogin.value), this.reqOpts)
      .map(data =>{
        console.log(data);
        this.userProvider.saveDataDevice(data);
        this.loggedIn = true;
        return this.loggedIn;
      },error => {
        console.log(error);
        this.loggedIn = false;
        return this.loggedIn;
      }
    );
  }

  logOut(): void {
    this.userProvider.removeDataDevice();
    this.loggedIn = false;
  }

  isLoggedIn(): boolean {
    return this.loggedIn;
  }

}



